'use client';

import { useEffect, useState } from 'react';
import { useAnchorWallet, useConnection } from '@solana/wallet-adapter-react';
import { Program, AnchorProvider, BN, Idl } from '@project-serum/anchor';
import { SystemProgram, PublicKey } from '@solana/web3.js';
import { PollAccount, CandidateAccount, StatusMessage } from '@/app/models/types';
import { PROGRAM_ID } from '@/app/utils/constants';
import { getPollPDA, getCandidatePDA, getVoterRecordPDA } from '@/app/utils/pdas';

// Import the IDL - adjust the path as needed
import idlFile from '../../target/idl/solana_voting_app.json';

const createProgramInstance = (
  idl: any,
  programId: PublicKey,
  provider: AnchorProvider
) => {
  // Force cast the IDL to the expected type
  return new Program(idl as any, programId, provider);
};

export const useVotingProgram = () => {
  const { connection } = useConnection();
  const wallet = useAnchorWallet();

  const [program, setProgram] = useState<Program | null>(null);
  const [loading, setLoading] = useState(false);
  const [status, setStatus] = useState<StatusMessage | null>(null);

  // Initialize the program when wallet connects
  useEffect(() => {
    if (wallet) {
      const provider = new AnchorProvider(
        connection,
        wallet,
        { commitment: 'processed' }
      );

      try {
        if (!idlFile || typeof idlFile !== 'object') {
          throw new Error('IDL file is not properly loaded');
        }

        // Check essential IDL properties
        if (!Array.isArray(idlFile.instructions) || idlFile.instructions.length === 0) {
          throw new Error('IDL is missing instructions');
        }

        const programInstance = createProgramInstance(idlFile, PROGRAM_ID, provider);
        setProgram(program);
        setStatus(null);
      } catch (err) {
        console.error('Failed to initialize program:', err);
        setStatus({
          message: 'Failed to initialize program',
          isError: true
        });
      }
    } else {
      setProgram(null);
    }
  }, [connection, wallet]);

  // Fetch all polls
  const fetchPolls = async (): Promise<PollAccount[]> => {
    if (!program) return [];
    
    setLoading(true);
    setStatus(null);
    
    try {
      const polls = await program.account.poll.all();
      
      // Convert the returned data to your PollAccount type
      const formattedPolls = polls.map(item => {
        // First cast to unknown, then to your PollAccount type
        return {
          publicKey: item.publicKey,
          account: {
            pollId: item.account.poll_id,
            description: item.account.description,
            pollStartTime: item.account.poll_start_time,
            pollEndTime: item.account.poll_end_time,
            candidateAmount: item.account.candidate_amount
          }
        } as PollAccount;
      });
      
      return formattedPolls;
    } catch (err) {
      console.error('Error fetching polls:', err);
      setStatus({
        message: 'Failed to fetch polls',
        isError: true
      });
      return [];
    } finally {
      setLoading(false);
    }
  };

  // Fetch candidates for a specific poll
  const fetchCandidates = async (pollId: BN): Promise<CandidateAccount[]> => {
    if (!program) return [];
    
    setLoading(true);
    setStatus(null);
    
    try {
      // Fetch all candidates
      const allCandidates = await program.account.candidate.all();
      
      // Filter for candidates of this specific poll and format them
      const formattedCandidates = allCandidates
        .filter(candidate => {
          // Implement proper filtering logic here
          // Example: check if candidate belongs to this poll
          return true; // Replace with actual condition
        })
        .map(item => {
          // First cast to unknown, then to your CandidateAccount type
          return {
            publicKey: item.publicKey,
            account: {
              candidateName: item.account.candidate_name,
              candidateVotes: item.account.candidate_votes
            }
          } as CandidateAccount;
        });
      
      return formattedCandidates;
    } catch (err) {
      console.error('Error fetching candidates:', err);
      setStatus({
        message: 'Failed to fetch candidates',
        isError: true
      });
      return [];
    } finally {
      setLoading(false);
    }
  };

  // Create a new poll
  const createPoll = async (description: string): Promise<boolean> => {
    if (!program || !wallet) return false;

    setLoading(true);
    setStatus(null);

    try {
      // Generate a unique poll ID using current timestamp
      const pollId = new BN(Date.now());

      // Set poll duration - from now to 7 days in the future
      const now = Math.floor(Date.now() / 1000);
      const oneWeekFromNow = now + (7 * 24 * 60 * 60);

      // Get the PDA for the poll
      const pollPDA = getPollPDA(pollId);

      // Call the initialize_poll instruction
      await program.methods
        .initializePoll(
          pollId,
          description,
          new BN(now),
          new BN(oneWeekFromNow)
        )
        .accounts({
          signer: wallet.publicKey,
          poll: pollPDA,
          systemProgram: SystemProgram.programId,
        })
        .rpc();

      setStatus({
        message: 'Poll created successfully!',
        isError: false
      });
      return true;
    } catch (err) {
      console.error('Error creating poll:', err);
      setStatus({
        message: `Failed to create poll: ${err}`,
        isError: true
      });
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Add a candidate to a poll
  const addCandidate = async (pollId: BN, candidateName: string): Promise<boolean> => {
    if (!program || !wallet) return false;

    setLoading(true);
    setStatus(null);

    try {
      // Get the PDAs
      const pollPDA = getPollPDA(pollId);
      const candidatePDA = getCandidatePDA(pollId, candidateName);

      // Call the initialize_candidate instruction
      await program.methods
        .initializeCandidate(
          pollId,
          candidateName
        )
        .accounts({
          signer: wallet.publicKey,
          poll: pollPDA,
          candidate: candidatePDA,
          systemProgram: SystemProgram.programId,
        })
        .rpc();

      setStatus({
        message: 'Candidate added successfully!',
        isError: false
      });
      return true;
    } catch (err) {
      console.error('Error adding candidate:', err);
      setStatus({
        message: `Failed to add candidate: ${err}`,
        isError: true
      });
      return false;
    } finally {
      setLoading(false);
    }
  };

  // Vote for a candidate
  const vote = async (pollId: BN, candidateName: string): Promise<boolean> => {
    if (!program || !wallet) return false;

    setLoading(true);
    setStatus(null);

    try {
      // Get the PDAs
      const pollPDA = getPollPDA(pollId);
      const candidatePDA = getCandidatePDA(pollId, candidateName);
      const voterRecordPDA = getVoterRecordPDA(pollId, candidateName, wallet.publicKey);

      // Call the vote instruction
      await program.methods
        .vote(
          pollId,
          candidateName
        )
        .accounts({
          signer: wallet.publicKey,
          poll: pollPDA,
          candidate: candidatePDA,
          voterRecord: voterRecordPDA,
          systemProgram: SystemProgram.programId,
        })
        .rpc();

      setStatus({
        message: 'Vote recorded successfully!',
        isError: false
      });
      return true;
    } catch (err) {
      console.error('Error voting:', err);
      setStatus({
        message: `Failed to vote: ${err}`,
        isError: true
      });
      return false;
    } finally {
      setLoading(false);
    }
  };

  return {
    program,
    loading,
    status,
    fetchPolls,
    fetchCandidates,
    createPoll,
    addCandidate,
    vote,
  };
};

export default useVotingProgram;